.App {
  text-align: center;
}
a {
  cursor: pointer;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.navbar-brand {
  font-family: 'Oswald', sans-serif;
}
.nav-cart-wrap {
  display: flex;
  justify-content: end;
  align-items: end;
  position: relative;
}
.nav-cart-num {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(255, 0, 157);
  width: 20px;
  height: 20px;
  color: white;
  border-radius: 50%;
  right: 5px;
  top: 9px;
  font-size: 12px;
}
.bg-body-tertiary {
  background-color: #282c34 !important;
}
.navbar-toggler {
  background-color: #b4b4b4;
}
.main-container {
  min-height: 100vh;
}
.main-header {
  display: flex;
  justify-content: center;
  align-items: center;
}
.main-title {
  margin-top: 15px;
  padding: 15px;
  text-align: left;
}
.main-product-wrap {
  padding: 0 10px;
}
.main-product {
  border-radius: 5px;
  border: 1px solid #e4e4d9;
  display: flex;
  flex-direction: row;
  margin-bottom: 10px;
  cursor: pointer;
}
.product-bestseller {
  position: absolute;
  background-color: rgb(237, 127, 10);
  color: rgb(255, 255, 255);
  padding: 4px 10px;
  top: 3px;
  left: 3px;
  border-radius: 5px;
  font-size: 12px;
  font-weight: 600;
}
.main-product-img {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 40%;
  padding: 5px;
}
.main-product-img img {
  width: 100%;
}
.main-product-info h5 {
  position: relative;
  font-size: 17px;
  font-weight: 700;
  margin-bottom: 5px;
  /* font-family: 'Oswald', sans-serif; */
}
.main-product-info span {
  position: relative;
  left: -4px;
}
.main-product-info-text {
  font-size: 14px;
  margin-top: 5px;
  margin-bottom: 0;
}
.main-product-info {
  margin-top: 15px;
  padding: 12px 14px;
  text-align: left;
  width: 60%;
  background-color: #f8f8f5;
}
.main-product-price {
  font-size: 20px;
  margin-bottom: 0;
  margin-top: 10px;
  /* text-align: right; */
  font-family: 'Oswald', sans-serif;
}
.showmore {
  margin: 20px 0;
}
.detail-container {
  background-color: #f2f2f2;
  padding: 10px;
}

.detail-product-img {
  position: relative;
  margin: 5px 5px 15px 5px;
}
.detail-product-img img {
  width: 100%;
  height: 100%;
}
.detail-bestseller {
  position: absolute;
  background-color: rgb(237, 127, 10);
  color: rgb(255, 255, 255);
  padding: 4px 10px;
  top: 6px;
  left: 6px;
  border-radius: 5px;
  font-size: 18px;
  font-weight: 600;
}
.detail-title {
  padding: 8px 10px;
  text-align: left;
  font-weight: 600;
  font-size: 20px;
}
.detail-price-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
}
.detail-product-price {
  font-size: 20px;
  margin-bottom: 0;
  margin-top: 10px;
  /* text-align: right; */
  font-weight: 700;
  font-family: 'Oswald', sans-serif;
}
.detail-qty {
  margin-top: 15px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.detail-qty-bt {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #9c9c9c;
  padding: 10px 10px;
  width: 50px;
  height: 50px;
  color: white;
  font-size: 20px;
  font-weight: 900;
}
.detail-qty-number {
  text-align: center;
  width: 50px;
  height: 50px;
  border: none;
}
.add-cart {
  margin: 20px 0;
  width: 95%;
  padding: 10px 0;
  border: none;
  border-radius: 20px;
  background-color: #ffbb00;
  font-weight: 500;
}
.add-cart2 {
  margin: 0px 0px 15px 0;
  width: 95%;
  padding: 10px 0;
  border: none;
  border-radius: 20px;
  background-color: #ff5900;
  font-weight: 500;
}
.cart-header {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 150px;
  background-color: cadetblue;
  margin-bottom: 15px;
}
footer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50px;
  background-color: #282c34;
  color: white;
  margin-top: 20px;
}
.button {
  user-select: none;
}
.qty-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 120px;
}
.cart-qty-bt {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #282c34;
  width: 25px;
  height: 25px;
  margin: 5px;
  color: white;
  font-size: 10px;
}
.viewed-top-title {
  text-align: left;
  font-size: 20px;
  margin-bottom: 10px;
  font-family: 'Oswald', sans-serif;
}
.viewed-title {
  font-size: 11px;
  font-weight: 700;
  line-height: 14px;
}
.viewed-price {
  font-size: 11px;
  font-weight: 700;
  color: red;
  margin-bottom: 20px;
}
footer p {
  margin-bottom: 0;
}
